import React from 'react';
import blueCode from '../images/blue-code.png';

const Intro = () => {
  const paperPlane = {
    position: 'relative',
    left: '0.2rem',
  }
  return (
    <div>
      <header className="front">
        <div className="front_logo">
          <h1>Hi, my name is Paul Ma</h1>
          <p>,f2e</p>
          <p><br/>reactrouterreduxgraphQLwebpack...</p>
          <a href="/" className="main-button"><i style={paperPlane} className="fa fa-paper-plane-o" aria-hidden="true"></i></a>
          <a className="sub-button"></a>
          <div className="scroll-down"><i className="fa fa-angle-down" aria-hidden="true"></i></div>
        </div>

        <img className="front_bg-photo" src={blueCode} alt="intro-background" />
      </header>
    </div>
  )
}

export default Intro;


